.hotel-banner {
	@include pos(rel,null);
	&:before {
		content: '';
		@include pos(abs, 0 0 null 0);
		height: 200px;
		background: linear-gradient(color-rgba($white,1),color-rgba($white,0));
		z-index: 5; } }
@mixin placelist {
	.place-list {
		margin-bottom: 20px;
		.row {
			@include media-breakpoint-down(lg) {
				margin: 0px -10px; } }
		.col-12 {
			margin-bottom: 30px;
			@include media-breakpoint-down(lg) {
				padding: 0px 10px;
				margin-bottom: 20px; } }
		.place-card {
			figure {
				@include pos(rel,null);
				@include bor-rad(5px);
				overflow: hidden;
				@include transition(all,0.3s);
				.place-img {
					display: block;
					img {
						width: 100%;
						height: 250px;
						object-fit: cover;
						@include media-breakpoint-down(lg) {
							height: 200px; }
						@include media-breakpoint-down(md) {
							height: 150px; }
						@include media-breakpoint-down(xs) {
							height: 200px; } } }
				figcaption {
					.place-name {
						margin: 0;
						padding: 0;
						a {
							@include pos(abs, 0 null 0 0);
							right: 0;
							background: color-rgba($black,0.3);
							@include flexbox;
							justify-content: center;
							align-items: center;
							align-content: center;
							@include fs(28rem);
							font-weight: 600;
							color: $white;
							@include media-breakpoint-down(md) {
								@include fs(18rem); } } } }
				&:hover {
					box-shadow: 0 20px 30px -10px color-rgba($black,0.2);
					margin-top: -5px; } } } } }
.hotel-page {
	padding: 30px 0;
	// border-top: 1px solid darken($white,10%)
	@include placelist;
	.nav-tabs {
		margin: 0 0 30px;
		padding: 0;
		border: none;
		@include media-breakpoint-down(sm) {
			display: block;
			white-space: nowrap;
			overflow: auto; }
		.nav-link {
			margin: 0;
			padding: 0;
			border: none;
			@include fs(18rem);
			font-weight: 600;
			color: darken($white,30%);
			@include media-breakpoint-down(sm) {
				@include fs(14rem);
				display: inline-block;
				vertical-align: top; }
			+ .nav-link {
				margin-left: 40px;
				@include media-breakpoint-down(sm) {
					margin-left: 20px; } }
			&:hover {
				color: darken($white,60%); }
			&.active {
				color: $main; } } }
	.hotel-list {
		.col-12 {
			margin-bottom: 30px;
			@include media-breakpoint-down(md) {
				margin-bottom: 15px; } }
		.hotel-card {
			border: 1px solid darken($white,10%);
			@include bor-rad(5px);
			// overflow: hidden
			figure {
				@include bor-rad(5px);
				overflow: hidden;
				@include transition(all,0.3s);
				.hotel-img {
					display: block;
					@include pos(rel,null);
					img {
						width: 100%; }
					.hotel-rating {
						@include pos(abs, null null 15px 0);
						padding: 5px 10px;
						background: $blue;
						color: $white;
						font-weight: 500;
						@include bor-rad(0 3px 3px 0);
						@include media-breakpoint-down(md) {
							@include fs(13rem); } } }
				figcaption {
					background: $white;
					padding: 20px 25px;
					@include media-breakpoint-down(md) {
						padding: 15px 15px; }
					.hotel-name {
						@include fs(20rem);
						font-weight: 600;
						line-height: 1.3;
						margin-bottom: 5px;
						@include media-breakpoint-down(md) {
							@include fs(16rem);
							font-weight: 700; }
						a {
							color: $main; } }
					.hotel-star {
						color: $amber;
						margin-bottom: 10px;
						.fa {
							+ .fa {
								margin-left: 3px; } } }
					.hotel-des {
						@include fs(14rem);
						color: darken($white,40%);
						@include media-breakpoint-down(md) {
							@include fs(13rem);
							color: darken($white,60%); } } }
				&:hover {
					box-shadow: 0 20px 30px -10px color-rgba($black,0.2);
					margin-top: -5px;
					figcaption {
						.hotel-name {
							a {
								color: $blue; } } } } } } }
	.hotel-info {
		margin-bottom: 30px;
		.hotel-rating {
			float: right;
			width: 150px;
			text-align: right;
			@include fs(18rem);
			padding: 10px 0;
			strong {
				@include fs(28rem);
				color: $blue;
				font-weight: 500; } }
		.hotel-name, .hotel-location {
			float: left;
			width: calc(100% - 150px); }
		.hotel-name {
			@include fs(28rem);
			line-height: 1.5; }
		.hotel-location {
			color: darken($white,40%);
			line-height: 1.5;
			margin-bottom: 5px;
			.fa {
				color: $blue; }
			a {
				font-weight: 500;
				color: $main;
				&:hover {
					color: $blue; } } }
		.hotel-star {
			color: $amber;
			display: inline-block;
			vertical-align: middle;
			@include fs(16rem);
			margin-left: 10px;
			.fa {
				margin-right: 3px; } } }
	.favorite-service {
		@include bor-rad(5px);
		background: lighten($blue,45%);
		@include flexbox;
		justify-content: center;
		align-items: center;
		align-content: center;
		padding: 20px;
		margin-bottom: 30px;
		@include media-breakpoint-down(md) {
			flex-wrap: wrap;
			padding: 10px 15px; }
		.label {
			width: 250px;
			font-weight: 500;
			color: $blue;
			@include media-breakpoint-down(md) {
				width: 200px;
				@include fs(14rem); }
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-bottom: 10px; } }
		.service-list {
			width: calc(100% - 250px);
			@include flexbox;
			@include media-breakpoint-down(md) {
				width: calc(100% - 200px);
				flex-wrap: wrap; }
			@include media-breakpoint-down(xs) {
				width: 100%; }
			.service-card {
				width: 25%;
				padding: 0px 10px;
				text-align: center;
				@include media-breakpoint-down(xs) {
					width: 50%;
					margin-bottom: 20px; }
				.icon {
					margin-bottom: 10px; }
				.des {
					@include fs(14rem);
					color: $main;
					font-weight: 500;
					max-width: 120px;
					margin: 0 auto;
					@include media-breakpoint-down(md) {
						@include fs(12rem); }
					@include media-breakpoint-down(xs) {
						@include fs(11rem); } } } } }
	.room-list {
		.col-12 {
			margin-bottom: 30px; }
		.room-card {
			figure {
				// +flexbox
				border: 1px solid darken($white,10%);
				@include bor-rad(5px);
				overflow: hidden;
				@include transition(all,0.3s);
				.room-img {
					display: block;
					// width: 50%
					overflow: hidden;
					@include pos(rel,null);
					img {
						width: 100%;
						height: 180px;
						object-fit: cover;
						@include bor-rad(5px 5px 0 0);
						@include transition(all,0.3s); }
					.btn-viewdetail {
						@include pos(abs, 50% null null 50%);
						@include transform(translateX(-50%)translateY(-50%));
						background: $white;
						color: $blue;
						border: 2px solid $blue;
						@include bor-rad(50px);
						line-height: 36px;
						padding: 0px 20px;
						// font-weight: 500
						white-space: nowrap;
						@include transition(all,0.3s);
						opacity: 0;
						&:hover {
							background: $blue;
							color: $white; } } }
				figcaption {
					// width: 50%
					padding: 15px 15px 40px;
					@include pos(rel,null);
					.room-title {
						@include fs(16rem);
						font-weight: 600;
						margin-bottom: 5px;
						a {
							color: $main; } }
					.room-des {
						@include fs(14rem);
						color: darken($white,60%); }
					.btn-viewprice {
						@include pos(abs, null 0 0 0);
						background: darken($white,10%);
						color: $main;
						text-align: center;
						// font-weight: 600
						@include fs(14rem);
						padding: 10px 0; } }
				&:hover {
					box-shadow: 0 20px 30px -10px color-rgba($black,0.2);
					margin-top: -5px;
					.room-img {
						img {
							filter: brightness(70%);
 }							// +transform(scale3d(1.2,1.2,1))
						.btn-viewdetail {
							opacity: 1; } }
					figcaption {
						.btn-viewprice {
							background: $blue;
							color: $white;
							&:hover {
								background: $dblue; } } } } } }

		.room-group {
			@include flexbox;
			flex-wrap: wrap;
			border: 1px solid darken($white,10%);
			margin-bottom: 30px;
			.room-type {
