				@include fs(14rem);
				font-weight: 500;
				width: 100%;
				border-bottom: 1px solid darken($white,10%); }
			.room-intro {
				width: 400px;
				padding: 10px;
				background: darken($white,5%);
				figure {
					@include flexbox;
					flex-wrap: wrap; }
				.room-img {
					display: block;
					width: 50%; }
				figcaption {
					width: 50%;
					padding-left: 15px;
					.room-des {
						ul {
							li {
								// padding-left: 20px
								@include fs(14rem);
								@include pos(rel,null);
								.fa {
									@include pos(abs, 3px null null 0); }
								&:before {
									display: none; } } } } } }
			.room-booking {
				width: calc(100% - 400px);
				border-left: 1px solid darken($white,10%);
				.table-head {
					@include fs(13rem);
					font-weight: 700;
					background: darken($white,3%);
					border-bottom: 1px solid darken($white,10%);
					@include flexbox;
					.baogom, .succhua, .giaphong, .sl, .datphong {
