						border-left: 1px solid darken($white,10%); } }
				.baogom {
					width: calc(100% - 90px - 180px - 80px - 160px); }
				.succhua {
					width: 90px; }
				.giaphong {
					width: 180px; }
				.sl {
					width: 80px; }
				.datphong {
					width: 160px; }
				.table-room {
					@include fs(13rem);
					.room {
						@include flexbox;
						// border-bottom: 1px solid darken($white,10%)
						&:last-child {
 }							// border-bottom: none
						&:hover {
							background: lighten($blue,45%); }
						.succhua, .giaphong, .sl, .datphong {
							border-left: 1px solid darken($white,10%); }
						.baogom, .succhua, .giaphong, .sl, .datphong {
							padding: 10px; }
						.baogom {
							ul {
								li {
									// padding-left: 10px
									// +fs(14rem)
									// +pos(rel,null)
									// &:before
									// 	content: ''
									// 	display: block
									// 	width: 5px
									// 	height: 5px
									// 	+bor-rad(10px)
									// 	background: darken($white,60%)
 } } }									// 	+pos(abs, 7px null null 0)
						.succhua {
							.fa {
								color: $blue; } }
						.giaphong {
							text-align: right;
							.old {
								@include fs(13rem);
								text-decoration: line-through;
								color: darken($white,40%);
								line-height: 1; }
							.new {
								@include fs(18rem);
								font-weight: 600;
								color: lighten($red,10%);
								line-height: 1.5; }
							.des {
								@include fs(11rem);
								color: darken($white,40%); } }
						.sl {
							select {
								border: 1px solid darken($white,10%);
