						.datphong {
							.btn-book {
								display: block;
								background: $blue;
								color: $white;
								@include fs(13rem);
								@include bor-rad(5px);
								padding: 6px 0px;
								text-align: center;
								&:hover {
									background: darken($blue,10%); }
								&:before {
									content: '\f044';
									margin-right: 5px; } } } } } } } }
	.product-tabs {
 } }		// margin-bottom: 20px
.hotel-room {}

.hotel-compare {
	margin-bottom: 30px;
	.compare-table {
		@include flexbox;
		@include media-breakpoint-down(md) {
			display: block;
			overflow: auto;
			white-space: nowrap; }
		.compare-field {
			width: 20%;
			@include media-breakpoint-down(md) {
				width: 200px;
				display: inline-block;
				display: none; } }
		.empty {
			border-bottom: 1px solid darken($white,10%);
			height: 203px; }
		.review, .area, .wifi, .breakfast, .button {
			height: 40px;
			@include flexbox;
			align-items: center;
			align-content: center;
			padding: 0px 10px;
			@include fs(14rem);
			// font-weight: 500
			border-bottom: 1px solid darken($white,10%);
			.on {
				color: $blue;
				font-weight: 500; }
			.off {
				color: darken($white,20%);
				text-decoration: line-through;
 } }				// font-weight: 700
		.button {
			height: 60px;
			border: none; }
		.compare-list {
			width: 80%;
			@include flexbox;
			border-left: 1px solid darken($white,10%);
			@include media-breakpoint-down(md) {
				width: 800px; }
			.compare-card {
				width: 25%;
				border-right: 1px solid darken($white,10%);
				&.selected {
					border: 2px solid $blue;
					box-shadow: 0 10px 20px -10px color-rgba($black,0.5); }
				figure {
					.hotel-img {
						padding: 10px; }
					figcaption {
						.hotel-name {
							// +fs(14rem)
							height: 0.875*1.3*2;
							padding: 0px 10px;
							@include fs(14rem);
							font-weight: 500;
							line-height: 1.3;
							-webkit-line-clamp: 2;
							-webkit-box-orient: vertical;
							overflow: hidden;
							text-overflow: ellipsis;
							margin-bottom: 5px;
							a {
								color: $main; } }
						.hotel-star {
							color: $amber;
							@include fs(13rem);
							padding: 0px 10px;
							margin-bottom: 5px;
							.fa {
								+ .fa {
									margin-left: 3px; } } }
						.compare-info {
							border-top: 1px solid darken($white,10%);
 }							// border-bottom: 1px solid darken($white,20%)
						.button {
							padding: 0px 10px; }
						.btn-view {
							text-align: center;
							line-height: 40px;
							@include bor-rad(5px);
							background: $blue;
							color: $white;
							@include fs(14rem);
							// font-weight: 600
							display: block;
							width: 100%;
							&:hover {
								background: lighten($blue,10%); } } } } } } } }
.hotel-service {
	margin-bottom: 30px;
	.service-col {
		.service-title {
			@include fs(16rem);
			font-weight: 500;
			margin-bottom: 10px;
			.fa {
				font-weight: normal; } }
		ul.service-list {
			// padding: 10px
			// +bor-rad(5px)
			// background: darken($white,10%)
			li {
				color: $main;
				margin-bottom: 5px;
				@include fs(14rem);
				font-weight: 500;
				span {
					display: block;
					color: darken($white,40%); } } } } }
.hotel-description {
	margin-bottom: 30px;
	.hotel-des {
		margin-bottom: 30px; }
	.small-title {
		@include fs(18rem);
		font-weight: 700;
		margin-bottom: 10px; }
	.specify-list {
		.specify-col {
			@include flexbox;
			flex-wrap: wrap;
			margin-bottom: 20px;
			.icon {
				width: 50px; }
			.content {
				width: calc(100% - 50px);
				padding-left: 20px;
				border-left: 1px solid darken($white,10%);
				@include fs(14rem);
				.title {
					font-weight: 500;
					margin-bottom: 5px; }
				ul {
					padding-left: 0;
					list-style-type: none;
					@include flexbox;
					flex-wrap: wrap;
					li {
						width: 33.33%;
						.ic {
							margin-right: 5px; } } } } } } }



.hotel-picture {
	margin-bottom: 40px;
	.picture-slide {
		margin: 0px -10px;
		.item {
			padding: 0px 10px; }
		.pic {
			@include pos(rel,null);
			overflow: hidden;
			display: block;
			color: $white;
			text-shadow: 0 3px 3px color-rgba($black,0.1);
			img {
				@include transition(all,0.3s); }
			&:hover {
				img {
					@include transform(scale3d(1.1,1.1,1)); } }
			.caption {
				@include pos(abs, null 0px 0px 0px);
				padding: 10px;
				@include fs(14rem);
				font-weight: 500;
				background: linear-gradient(color-rgba($black,0),color-rgba($black,0.5)); } } } }

.hotel-detail-list {
	.hotel-card {
		margin-bottom: 20px;
		figure {
			border: 1px solid darken($white,10%);
			@include flexbox;
			@include bor-rad(5px);
			overflow: hidden;
			flex-wrap: wrap;
			@include transition(all,0.3s);
			@include media-breakpoint-down(xs) {
				@include pos(rel,null); }
			&:hover {
				box-shadow: 0 10px 20px -10px color-rgba($black,0.3); }
			.hotel-img {
				display: block;
				width: 30%;
				@include pos(rel,null);
				height: 200px;
				@include media-breakpoint-down(xs) {
					width: 100%; }
				img {
					width: 100%;
					height: 200px;
					@include bor-rad(5px 0 0 5px);
					object-fit: cover;
					@include media-breakpoint-down(xs) {
						@include bor-rad(0); } }
				.hotel-rating {
					@include pos(abs, null null 20px 0);
					background: $blue;
					color: $white;
					@include fs(16rem);
					font-weight: 500;
					padding: 5px 10px; } }
			figcaption {
				width: 70%;
				background: darken($white,3%);
				@include flexbox;
				@include pos(rel,null);
				@include media-breakpoint-down(xs) {
					width: 100%;
					flex-wrap: wrap;
					@include pos(static,unset); }
				.colleft {
					width: 70%;
					padding: 15px;
					@include media-breakpoint-down(xs) {
						width: 100%;
						padding-bottom: 0; } }
				.hotel-name {
					font-weight: 500;
					@include fs(16rem);
					@include media-breakpoint-down(xs) {
						@include fs(14rem);
						font-weight: 600; }
					a {
						color: $main; } }
				.hotel-star {
					color: $amber;
					.fa {
						@include media-breakpoint-down(xs) {
							@include fs(13rem); }
						+ .fa {
							margin-left: 3px; } } }
				.hotel-location {
					@include fs(14rem);
					color: darken($white,40%);
					margin-bottom: 20px;
					@include media-breakpoint-down(xs) {
						@include fs(12rem);
						margin-bottom: 10px; }
					.fa {
						color: $blue; } }
				.hotel-feature {
					@include flexbox;
					@include pos(rel,null);
					margin-bottom: 10px;
					background: $white;
					// border: 1px solid $blue
					@include bor-rad(5px);
					.label {
						width: 90px;
						background: darken($white,10%);
						color: $main;
						@include fs(13rem);
						padding: 5px 10px;
						font-weight: 500;
						@include media-breakpoint-down(xs) {
							@include fs(11rem); } }
					> .feature-list {
						@include flexbox;
						align-items: center;
						align-content: center;
						width: calc(100% - 90px);
						white-space: nowrap;
						overflow: hidden;
						@include pos(rel,null);
						padding: 0px 10px;
						&:after {
							content: '';
							width: 20px;
							@include pos(abs, 0 0 0 null);
							background: linear-gradient(to right, color-rgba($white,0),color-rgba($white,1));
							@include bor-rad(5px); }
						li {
							display: inline-block;
							@include fs(13rem);
							color: darken($white,40%);
							@include media-breakpoint-down(xs) {
								@include fs(11rem); }
							.fa, strong {
								margin-right: 3px;
								color: $blue; }
							+ li {
								margin-left: 10px; } } }
					.popup-tooltip {
						@include pos(abs, 120% null null 0);
						background: color-rgba($black,0.8);
						@include bor-rad(3px);
						padding: 15px;
						color: $white;
						z-index: 5;
						opacity: 0;
						visibility: hidden;
						&:before {
							content: '';
							display: block;
							width: 0;
							height: 0;
							border: 5px solid;
							border-top: none;
							border-color: transparent transparent color-rgba($black,0.8) transparent;
							@include pos(abs, -5px null null 10px); }
						.title {
							@include fs(14rem);
							margin-bottom: 10px;
							@include media-breakpoint-down(xs) {
								@include fs(12rem); } }
						.feature-list {
							@include flexbox;
							flex-wrap: wrap;
							min-width: 300px;
							@include media-breakpoint-down(xs) {
								min-width: 260px; }
							li {
								width: 50%;
								@include fs(14rem);
								@include media-breakpoint-down(xs) {
									@include fs(11rem); }
								.fa {
									margin-right: 5px;
									color: $blue; } } } }
					&:hover {
						.popup-tooltip {
							visibility: visible;
							opacity: 1; } } }
				.colright {
					width: 30%;
					background: $white;
					text-align: right;
					@include media-breakpoint-down(xs) {
						width: 100%;
						background: none;
						@include flexbox;
						flex-wrap: wrap;
						align-items: center;
						align-content: center;
						padding: 0px 15px 15px; } }
				.promotion {
					color: $white;
					font-weight: 500;
					display: inline-block;
					padding: 5px 30px;
					// margin: 15px -2px 15px 0
					@include pos(abs, 20px -10px null null);
					@include media-breakpoint-down(xs) {
						@include fs(13rem);
						padding: 5px 15px;
						right: -5px;
						top: 10px; }
					&.sale {
						background: lighten($red,10%); } }
				.hotel-price {
					padding: 0px 15px;
					margin-bottom: 20px;
					margin-top: 70px;
					@include media-breakpoint-down(xs) {
						margin: 0;
						padding: 0;
						order: 2;
						width: calc(100% - 100px); }
					.old {
						@include fs(14rem);
						font-weight: 500;
						color: darken($white,40%);
						text-decoration: line-through;
						@include media-breakpoint-down(xs) {
							@include fs(12rem); } }
					.new {
						color: lighten($red,10%);
						@include fs(20rem);
						font-weight: 600;
						@include media-breakpoint-down(xs) {
							@include fs(16rem); } } }
				.btn-view {
					display: block;
					background: $blue;
					text-align: center;
					color: $white;
					text-transform: uppercase;
					@include fs(14rem);
					font-weight: 500;
					line-height: 36px;
					margin: 0px 15px;
					// +bor-rad(5px)
					@include media-breakpoint-down(xs) {
						width: 100px;
						text-align: center;
						@include fs(13rem);
						order: 1;
						margin: 0;
						@include bor-rad(3px); }
					&:hover {
						background: darken($blue,10%); } } } } } }

