// USER SIDEBAR

.userpage {
	// .row
	// 	margin: 0px -5px
	// .col-xs-12
 }	// 	padding: 0px 5px
.user-sidebar {
	@include pos(rel,null);
	margin-top: 0;
	margin: 40px 0;
	@include media-breakpoint-down(md) {
		&:after {
			content: '\f107';
			font-family: 'fontawesome';
			@include pos(abs, 50% 15px null null);
			@include transform(translateY(-50%));
			color: $blue;
			@include fs(24rem); } }
	.userinfo {
		border: 1px solid lighten($blue,20%);
		background: lighten($blue,40%);
		@include bor-rad(5px);
		overflow: hidden;
		figure {
			&:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			@include flexbox;
			flex-wrap: wrap;
			.user-img {
				// display: none
				width: 80px;
				float: left;
				padding: 15px;
				@include media-breakpoint-down(lg) {
					width: 60px; } }
			figcaption {
				width: calc(100% - 80px);
				@include flexbox;
				flex-wrap: wrap;
				align-items: center;
				align-content: center;
				@include media-breakpoint-down(lg) {
					padding: 7px 15px; }
				.title {
					color: $main;
					opacity: 0.8;
					font-weight: 600;
					@include fs(13rem);
					width: 100%; }
				.user-name {
					text-transform: uppercase;
					font-weight: 700;
					// +fs(16rem)
					@include fs(14rem);
					color: $blue;
					width: 100%; }
				.user-rank {
					@include fs(13rem);
					font-weight: bold;
					color: color-rgba($white,0.8);
					display: none;
					.fa {
						color: $amber;
						// margin-right: 5px
						@include fs(16rem); } } } }
		.btn-muasi {
			margin: 0px 15px 15px;
			display: block;
			background: darken($white,10%);
			text-align: center;
			text-transform: uppercase;
			color: $blue;
			font-weight: 700;
			@include bor-rad(5px);
			padding: 5px 0;
			box-shadow: 0 3px 10px color-rgba($black,0.3); } } }
.user-sidelink {
	margin: 40px 0;
	ul {
		border-right: 1px solid darken($white,10%);
		li {
			display: block;
			&:before {
				display: none; }
			a {
				display: block;
				padding: 5px 0;
				font-weight: 500;
				color: darken($white,60%);
				// text-transform: uppercase
				@include pos(rel,null);
				@include fs(14rem);
				@include media-breakpoint-down(md) {
					@include fs(13rem); }
				&:after {
					content: '';
					display: block;
					width: 2px;
					@include pos(abs, 0 -1px 100% null);
					background: $blue;
					@include transition(all,0.5s); } }
			&:hover {
				a {
					color: $black;
					&:after {
						bottom: 0; } } }
			&.active {
				a {
					color: $blue;
					&:after {
						bottom: 0; } } } } } }

// USER PANEL

.user-page {
	// margin-top: 40px
	margin: 40px 0;
	// padding: 15px
	background: $white;
	@include pos(rel,null);
	@include media-breakpoint-down(md) {
		margin-top: 0; }
	.postname {
		@include fs(18rem);
		text-transform: uppercase;
		font-weight: 600;
		margin-bottom: 30px;
		border-left: 3px solid $blue;
		padding-left: 10px; }
	.posttitle {
		@include fs(15rem);
		line-height: 1.5;
		margin: 0 0 10px;
		min-height: 27px;
		font-weight: 600;
		// border-left: 0
		// padding-left: 0
		// &:before
		// 	content: '//'
		// 	color: darken($blue,5%)
 }		// 	margin-right: 10px
	.link {
		@include fs(13rem);
		color: $blue;
		background: lighten($blue,45%);
		border: 1px solid lighten($blue,30%);
		padding: 3px 10px;
		font-weight: 600;
		@include bor-rad(5px);
		@include pos(abs, 3px 0 null null);
		&:after {
			content: '\f105';
			font-family: 'fontawesome';
			margin-left: 10px; }
		&:hover {
			color: $white;
			background: $blue;
			border-color: $blue; } }
	.btn-addaddr {
		@include fs(13rem);
		color: $blue;
		background: lighten($blue,45%);
		border: 1px solid lighten($blue,30%);
		padding: 3px 10px;
		font-weight: 600;
		@include bor-rad(5px);
		@include pos(abs, 3px 0 null null);
		&:after {
			content: '\f044';
			font-family: 'fontawesome';
			margin-left: 10px;
			font-weight: normal; }
		&:hover {
			color: $white;
			background: $blue;
			border-color: $blue; } }
	.table {
		@include fs(14rem);
		background: $white;
		margin-bottom: 0;
		border: 1px solid darken($white,10%);
		@include bor-rad(5px);
		overflow: hidden;
		color: darken($white,60%);
		a {
			color: $main;
			font-weight: 500;
			// &:before
			// 	content: '\f00e'
			// 	font-family: 'fontawesome'
			// 	margin-right: 5px
			&:hover {
				color: $blue; } }
		.point {
			font-weight: 700;
			color: $blue; }
		.link-detail {
			color: $blue;
			font-weight: normal;
			margin-left: 10px;
			&:hover {
				text-decoration: underline; } }
		th {
			border: none;
			background: darken($white,5%);
			color: $main;
			white-space: nowrap;
			border-bottom: 1px solid darken($white,10%); }
		td {
			border: none;
			@include fs(13rem);
			vertical-align: middle;
			.alert-success, .alert-warning, .alert-primary, .alert-info {
				background: none;
				font-weight: 500;
				filter: brightness(150%); } }
		th, td {
			white-space: nowrap;
			padding: 7px 15px;
			&:nth-child(3) {
				white-space: normal;
				@include media-breakpoint-down(sm) {
					white-space: nowrap; } } }
		tr {
			&:nth-child(even) {
				background: darken($white,3%); } } } }
.shipping-progress {
	@include flexbox;
	margin-bottom: 30px;
	.step {
		width: 20%;
		text-align: center;
		padding: 0px 20px;
		@include pos(rel,null);
		.icon {
			width: 60px;
			height: 60px;
			@include bor-rad(50px);
			line-height: 54px;
			margin: 0px auto 30px;
			@include fs(24rem);
			color: darken($white,40%);
			border: 2px solid darken($white,20%);
			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 4px;
				background: darken($white,20%);
				margin-top: 20px;
				@include pos(abs, null 0 null 0);
 } }				// +transform(translateX(-50%))
		.status {
			font-weight: bold;
			@include fs(13rem);
			color: $blue;
			line-height: 1.3; }
		&:first-child {
			.icon {
				// &:after
 } }				/// display: none
		&.active {
			.icon {
				color: $white;
				background: $blue;
				border-color: $blue;
				&:after {
					background: $blue; } } } } }
.account-info {
	@include pos(rel,null);
	margin-bottom: 15px;
	// +media-breakpoint-down(md)
	// 	margin-bottom: 20px
	.info-detail {
		border: 1px solid darken($white,10%);
		background: darken($white,3%);
		@include bor-rad(5px);
		padding: 15px 20px;
		@include fs(14rem);
		line-height: 1;
		.group {
			margin-bottom: 10px;
			@include flexbox;
			flex-wrap: wrap;
			&:last-child {
				margin-bottom: 0; }
			.label {
				width: 150px;
				cursor: pointer;
				@include media-breakpoint-down(xs) {
					width: 100%;
					color: darken($white,40%); } }
			.detail {
				width: calc(100% - 150px);
				font-weight: 700;
				@include media-breakpoint-down(xs) {
					width: 100%;
					font-weight: bold; }
				&.point {
					color: $blue;
					font-weight: bold; } }
			&.money {
				.label {
					width: auto;
					float: left;
					margin-right: 10px; }
				.detail {
					width: auto;
					float: left; } } } } }
.account-form {
	width: 50%;
	@include media-breakpoint-down(lg) {
		width: 60%; }
	@include media-breakpoint-down(md) {
		width: 70%; }
	@include media-breakpoint-down(xs) {
		width: 100%; }
	.form-group {
		margin-bottom: 15px;
		label {
			text-transform: uppercase;
			@include fs(13rem);
			font-weight: 500;
			color: darken($white,60%);
			margin-bottom: 5px; }
		input, select {
			width: 100%;
			border: 1px solid darken($white,15%);
			padding: 10px 15px;
			font-size: 0.875rem;
			-webkit-transition: all, 0.3s;
			@include transition(all, 0.3s);
			@include bor-rad(5px);
			&:hover, &:active, &:focus {
				border-color: darken($white,40%); } } }
	.gender-select {
		margin-bottom: 0;
		ul {
			padding: 10px 0 0;
			&:after {
				display: table;
				content: '';
				width: 100%;
				clear: both; }
			li {
				width: 50%;
				float: left; } }
		input[type="radio"] {
			+ label {
				// font-weight: 600
				color: $main;
				text-transform: none;
				font-weight: 600;
				@include fs(14rem);
				@include pos(rel,null);
				@include transition(all,0.5s);
				&:hover {
					color: $black; } } } }
	.birthday {
		ul {
			margin: 0 -10px;
			li {
				width: 33.33%;
				float: left;
				padding: 0 10px;
				@include media-breakpoint-down(xs) {
					select {
						@include fs(14rem);
						padding: 10px 10px; } } } } }
	.check-group {
		margin-bottom: 15px; }
	.changepw-form {
		display: none; }
	input[type='submit'] {
		color: $white;
		background: $blue;
		padding: 0 20px;
		border: none;
		@include fs(14rem);
		line-height: 36px;
		text-transform: uppercase;
		margin: 5px 0 0px;
		@include transition(all,0.3s);
		@include bor-rad(5px);
		&:hover {
			background: lighten($blue,10%); } } }

