.cart-info {
	border: 1px solid darken($white,20%);
	@include bor-rad(5px);
	height: 100%;
	.title {
		font-weight: bold;
		text-transform: uppercase;
		color: $blue;
		padding: 10px;
		border-bottom: 1px solid darken($white,10%); }
	.cart-detail {
		padding: 10px;
		.form-group {
			@include flexbox;
			flex-wrap: wrap;
			@include fs(13rem);
			&:last-child {
				margin-bottom: 0; }
			.label {
				width: 150px;
				font-weight: bold; }
			.value {
				width: calc(100% - 150px); } } }
	.alert-success, .alert-warning, .alert-primary, .alert-info {
		background: none;
		font-weight: 600;
		filter: brightness(150%);
 } }		// text-transform: uppercase

.shipping-step {
	@include flexbox;
	margin-bottom: 20px;
	.step {
		width: 33.33%;
		@include pos(rel,null);
		@include flexbox;
		flex-wrap: wrap;
		justify-content: center;
		&:before {
			content: '';
			display: block;
			width: 100%;
			height: 3px;
			background: darken($white,20%);
			@include pos(abs, 40px 50% null null);
			@include transform(translateY(-1px)); }
		&:first-child {
			&:before {
				display: none; } }
		.icon {
			@include flexbox;
			width: 80px;
			height: 80px;
			@include bor-rad(50%);
			justify-content: center;
			align-items: center;
			border: 3px solid darken($white,20%);
			@include pos(rel,null);
			z-index: 1;
			background: $white;
			img {
				max-width: 60%;
				max-height: 60%; } }
		.name {
			width: 100%;
			text-align: center;
			padding: 10px 0;
			font-weight: 500;
			color: darken($white,60%); }
		&.active {
			&:before {
				background: $blue; }
			.icon {
				border-color: $blue; }
			.name {
				color: $blue; } } } }

.qa-table {
	border: 1px solid darken($white,10%);
	width: 100%;
	@include bor-rad(5px);
	th, td {
		padding: 10px;
		@include fs(14rem);
		border-bottom: 1px solid darken($white,10%);
		// &:first-child
		// 	width: 40%
		font-weight: 500;
		// border-left: 1px solid darken($white,10%)
		@include media-breakpoint-down(xs) {
			@include fs(13rem);
			line-height: 1.3; }
		&:first-child {
			border-left: none; }
		&:last-child {
			@include media-breakpoint-down(xs) {
				display: none; } } }
	a {
		color: $main;
		&:hover {
			color: $blue; } }
	th {
		font-weight: 500;
		border-bottom: 2px solid $blue;
		background: lighten($blue,40%);
		@include media-breakpoint-down(xs) {
			&:first-child {
				@include fs(0); } } } }
