.support-page {
	padding: 50px 0;
	.faq-filter {
		margin-bottom: 20px;
		@include flexbox;
		justify-content: space-between;
		@include media-breakpoint-down(xs) {
			flex-wrap: wrap; }
		.amount {
			@include fs(14rem);
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-bottom: 5px; }
			strong {
				margin: 0px 0px 0px 5px;
				color: $black; } }
		.search-faq {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include media-breakpoint-down(xs) {
				width: 100%; }
			select {
				padding: 5px 10px;
				@include bor-rad(5px);
				border: 1px solid darken($white,10%);
				@include fs(14rem);
				@include media-breakpoint-down(xs) {
					width: 100px; } }
			input {
				padding: 6px 10px;
				@include bor-rad(5px);
				border: 1px solid darken($white,10%);
				@include fs(14rem);
				margin-left: 5px;
				@include media-breakpoint-down(xs) {
					width: calc(100% - 100px - 5px - 5px - 70px); } }
			.searchbutton {
				margin-left: 5px;
				padding: 5px 20px;
				font-weight: 500;
				@include bor-rad(5px);
				background: $blue;
				color: $white;
				@include fs(14rem);
				@include media-breakpoint-down(xs) {
					width: 70px; } } } }
	.btn-wrap {
		@include flexbox;
		justify-content: flex-end;
		margin-bottom: 30px; }
	.qa-table {
		margin-bottom: 30px; }
	.other-qa {
		padding-top: 30px; }
	.faq-title {
		color: $blue;
		margin-bottom: 15px;
		font-weight: 600;
		@include fs(16rem); } }
.support-nav {
	margin-bottom: 30px;
	ul {
		@include flexbox;
		// justify-content: center
		li {
			+ li {
				margin-left: 10px; }
			a {
				display: block;
				color: $blue;
				border: 1px dashed $blue;
				padding: 6px 20px;
				@include bor-rad(5px);
				@include fs(14rem);
				text-transform: uppercase;
				font-weight: 500; }
			&.active {
				a {
					background: $blue;
					color: $white;
					border: 1px solid $blue; } } } } }
.btn-sendmess {
	display: inline-block;
	padding: 8px 20px;
	@include bor-rad(5px);
	background: $blue;
	color: $white;
	@include fs(14rem);
	&:hover {
		color: $white;
		background: darken($blue,10%); }
	&:before {
		content: '\f044';
		margin-right: 5px; } }
.send-mess {
	@include bor-rad(5px);
	padding: 20px;
	background: darken($white,3%);
	// margin-bottom: 40px
	display: none;
	@include media-breakpoint-down(xs) {
		padding: 15px; }
	.module-title {
		@include fs(16rem);
		font-weight: 700;
		margin-bottom: 15px;
		@include media-breakpoint-down(xs) {
			@include fs(14rem);
			margin-bottom: 10px; } }
	.form-group {
		margin-bottom: 10px;
		input, textarea {
			@include bor-rad(5px);
			border: 1px solid darken($white,10%);
			padding: 8px 15px;
			width: 100%; } }
	input[type="reset"] {}
	input[type="submit"] {
		@include bor-rad(5px);
		border: none;
		display: inline-block;
		padding: 8px 30px;
		background: $blue;
		color: $white;
		&:hover {
			background: darken($blue,10%); } } }
