.bookingpage {
	header, footer {
		display: none; } }
.booking-header {
	background: $blue;
	// border-bottom: 1px solid darken($white,10%)
	.sitelogo {
		height: 50px;
		@include flexbox;
		align-items: center;
		align-content: center;
		float: left;
		a {
			display: block; }
		img {
			height: 30px;
			filter: grayscale(100%) brightness(400%); } }
	.hotline {
		float: right;
		background: darken($blue,5%);
		a {
			color: $white;
			padding: 0 20px;
			height: 50px;
			@include flexbox;
			justify-content: center;
			align-items: center;
			align-content: center;
			color: $white;
			font-weight: 500;
			&:before {
				content: '\f095';
				@include fs(24rem);
				display: inline-block;
				vertical-align: middle;
				margin-right: 10px;
				font-weight: normal; } } } }

.booking-footer {
	border-top: 1px solid darken($white,20%);
	background: darken($white,5%);
	margin-top: 30px;
	.copyright {
		text-align: center;
		@include fs(14rem);
		color: darken($white,60%);
		padding: 15px 0; } }
// BOOKING STEP

.booking-step {
	background: darken($white,0%);
	margin: 15px 0 30px;
	overflow: hidden;
	@include bor-rad(5px);
	border: 1px solid darken($white,10%);
	.step {
		width: 33.33%;
		float: left;
		line-height: 40px;
		padding: 0 0 0 25px;
		@include pos(rel,null);
		// border: 1px solid darken($white,20%)
		&:first-child {}
		&:last-child {}
		@include media-breakpoint-down(md) {
			width: 100%;
			display: none; }
		&:before {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-top: 23px solid transparent;
			border-bottom: 23px solid transparent;
			border-right: 0 solid transparent;
			border-left: 16px solid darken($white,10%);
			@include pos(abs, -2px -1px null null);
			@include transform(translateX(15px));
			z-index: 10; }
		&:after {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-top: 23px solid transparent;
			border-bottom: 23px solid transparent;
			border-right: 0 solid transparent;
			border-left: 15px solid darken($white,0%);
			@include pos(abs, -2px 1px null null);
			@include transform(translateX(15px));
			z-index: 11; }
		&:last-child {
			&:before, &:after {
				display: none; } }
		.number {
			display: inline-block;
			vertical-align: middle;
			font-weight: 600;
			@include fs(16rem);
			line-height: 1; }
		.name {
			display: inline-block;
			vertical-align: middle;
			padding-left: 10px;
			@include fs(13rem);
			@include media-breakpoint-down(md);
			font-weight: 500;
			// text-transform: uppercase
			color: darken($white,60%); }
		&.active {
			background: darken($white,5%);
			@include media-breakpoint-down(md) {
				display: block; }
			&:after {
				border-left-color: darken($white,5%); }
			.number {
				font-weight: bold;
				color: $dblue; }
			.name {
				color: $dblue; }
			&.finish {
				@include media-breakpoint-down(md) {
					display: none; } } }
		&.finish {
			background: darken($white,10%);
			&:after {
				border-left-color: darken($white,10%); }
			@include media-breakpoint-down(xs) {
				&:last-child {
					display: block; } }
			.number {
				display: inline-block;
				// background: darken($white,60%)
				background: none;
				color: darken($white,60%);
				@include fs(0);
				&:before {
					content: '\f00c';
					font-family: 'fontawesome';
					@include fs(15rem); } }
			.name {
				color: darken($white,60%); } } } }

.booking-page {
	> .row {
		margin: 0px -10px;
		> .col-12 {
			padding: 0px 10px; } }
	.hotel-info {
		border: 1px solid darken($white,10%);
		padding: 10px;
		margin-bottom: 20px;
		@include bor-rad(5px);
		figure {
			@include flexbox;
			.hotel-img {
				width: 40%; }
			figcaption {
				width: 60%;
				padding-left: 15px;
				.hotel-name {
					@include fs(16rem);
					font-weight: 500;
					a {
						color: $blue; } }
				.hotel-star {
					color: $amber;
					@include fs(14rem);
					.fa {
						+ .fa {
							margin-left: 3px; } } }
				.hotel-location {
					@include pos(rel,null);
					@include fs(13rem);
					color: darken($white,40%);
					padding-left: 15px;
					margin-bottom: 10px;
					.fa {
						@include pos(abs, 2px null null 0);
						color: $blue; } }
				.hotel-des {
					margin-bottom: 0px;
					ul {
						@include flexbox;
						flex-wrap: wrap;
						li {
							// width: 33.33%
							@include fs(14rem);
							@include flexbox;
							flex-wrap: wrap;
							margin-bottom: 5px;
							.label {
								width: 150px;
								@include fs(13rem);
								color: darken($white,40%); }
							.value {
								@include fs(13rem);
								width: calc(100% - 150px);
								font-weight: 500; } } } }
				.btn-changeroom {
					display: inline-block;
					// line-height: 30px
					@include fs(13rem);
					color: $blue;
					// background: $blue
					@include bor-rad(3px);
					&:after {
						content: '\f105';
						margin-left: 5px; } } } } }
	.hotel-feature {
		border: 1px solid darken($white,10%);
		padding: 10px;
		background: darken($white,3%);
		@include bor-rad(5px);
		margin-bottom: 20px;
		.hotel-rating {
			border-bottom: 1px solid darken($white,10%);
			padding-bottom: 10px;
			margin-bottom: 10px;
			strong {
				color: $blue; } }
		.hotel-service {
			margin-bottom: 0;
			.title {
				font-weight: 500;
				margin-bottom: 10px; }
			ul {
				@include flexbox;
				flex-wrap: wrap;
				margin: 0px -10px;
				li {
					width: 33.33%;
					padding: 0px 10px;
					@include fs(13rem);
					margin-bottom: 3px; } } } }
	.form-title {
		@include fs(16rem);
		font-weight: 600;
		margin-bottom: 10px; }
	.booking-form {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		padding: 10px;
		margin-bottom: 20px;
		.form-group {
			margin-bottom: 10px;
			label {
				@include fs(13rem);
				margin-bottom: 3px; }
			input, select, textarea {
				width: 100%;
				@include bor-rad(3px);
				@include fs(14rem);
				padding: 10px 15px;
				border: 1px solid darken($white,10%);
				&::-webkit-input-placeholder {
					color: $main;
					opacity: 0.5; } }
			select {
				padding: 9px 15px; }
			ul {
				@include flexbox;
				flex-wrap: wrap;
				li {
					width: 50%;
					label {
						@include fs(14rem); } } } }
		.booking-for {
			margin-bottom: 10px; }
		.sub-form {
			background: darken($white,3%);
			padding: 10px;
			@include bor-rad(5px);
			margin-bottom: 10px;
			display: none; }
		.form-des {
			@include fs(14rem);
			color: darken($white,60%);
			margin-bottom: 10px; }
		.module-title {
			@include fs(14rem);
			margin-bottom: 10px;
			color: darken($white,60%); }
		.btn-showform {
			@include fs(14rem);
			color: $blue;
			margin-bottom: 5px;
			cursor: pointer;
			@include transition(all,0.3s);
			&:hover {
				color: $red; }
			&:before {
				content: '\f055';
				margin-right: 5px; } }
		.require-form {
			.form-wrap {
				@include bor-rad(5px);
				border: 1px solid darken($white,10%);
				padding: 10px;
				margin-bottom: 10px;
				display: none;
				.form-group {
					border-bottom: 1px solid darken($white,10%);
					padding-bottom: 5px;
					&:last-child {
						border: none;
						padding-bottom: 0; } } } }
		.duadon {
			margin-bottom: 10px; }
		.form-duadon {
			display: none; } }
	.btn-wrap {
		.btn-back {
			float: left;
			color: $blue;
			@include fs(14rem);
			// font-weight: 600
			line-height: 40px;
			&:hover {
				color: $red; }
			&:before {
				content: '\f104';
				margin-right: 5px; } }
		.btn-continue {
			float: right;
			@include bor-rad(5px);
			line-height: 40px;
			padding: 0px 25px;
			color: $white;
			background: $blue;
			// font-weight: 600
			&:hover {
				background: darken($blue,10%); }
			&:after {
				content: '\f105';
				margin-left: 5px; } } }
	.booking-info {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		padding: 10px;
		margin-bottom: 20px;
		.booking-date {
			@include flexbox;
			padding: 10px 0 20px;
			border-bottom: 1px solid darken($white,10%);
			.from {
				width: 50%;
				@include flexbox;
				justify-content: center;
				@include pos(rel,null);
				&:after {
					content: '\f105';
					@include pos(abs, 50% 0 null null);
					@include transform(translateY(-50%));
					@include fs(24rem);
					line-height: 1;
 } }					// color: $blue
			.to {
				width: 50%;
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center; }
			.date {
				@include fs(36rem);
				line-height: 1;
				font-weight: 600;
				// color: darken($white,60%)
				color: $blue; }
			.des {
				@include fs(12rem);
				line-height: 1.3;
				color: darken($white,40%);
				@include flexbox;
				align-items: center;
				align-content: center;
				padding-left: 10px; } }
		.room-info {
			padding: 10px 0 5px;
			.room-name {
				// +fs(16rem)
				font-weight: 500;
				margin-bottom: 5px; }
			.room-des {
				ul {
					li {
						padding-left: 15px;
						@include pos(rel,null);
						@include fs(13rem);
						color: darken($white,40%);
						&:before {
							content: '';
							display: block;
							width: 6px;
							height: 6px;
							@include bor-rad(50px);
							background: darken($white,60%);
							@include pos(abs, 7px null null 0); } } } } } }
	.booking-amount {
		@include bor-rad(5px);
		margin-bottom: 20px;
		border: 1px solid darken($white,10%);
		.group {
			padding: 10px;
			@include flexbox;
			border-bottom: 1px solid darken($white,10%);
			&:last-child {
				border-bottom: none; }
			.label {
				width: calc(100% - 150px);
				@include fs(14rem);
				font-weight: 500;
				.des {
					@include fs(12rem);
					color: darken($white,40%); } }
			.value {
				width: 150px;
				text-align: right;
				.price {
					.old {
						@include fs(12rem);
						text-decoration: line-through;
						color: darken($white,40%); }
					.new {
						font-weight: 600;
 } } } }						// color: $red
		.group {
			.payment-method {
				font-weight: 600;
				color: $blue; } }
		.total-amount {
			background: darken($white,5%);
			.value {
				font-weight: 600;
				color: lighten($red,10%);
				@include fs(18rem); } } }
	.booking-payment {
		@include bor-rad(5px);
		padding: 10px;
		margin-bottom: 20px;
		border: 1px solid darken($white,10%); } }
.method-list {
	.payment-method {
		margin-bottom: 10px;
		label {
			width: 100%;
			&:before, &:after {
				top: 40px;
				@include transform(translateY(-50%)); }
			.method-info {
				@include flexbox;
				width: 100%;
				border: 1px solid darken($white,10%);
				&:hover {
					border: 1px solid darken($white,30%); }
				.method-img {
					width: 80px;
					height: 80px;
					@include flexbox;
					justify-content: center;
					align-items: center; }
				.method-detail {
					width: calc(100% - 80px);
					border-left: 1px solid darken($white,10%);
					padding: 10px;
					.method-name {
						@include fs(14rem);
						font-weight: 600;
						margin-bottom: 5px;
						@include media-breakpoint-down(xs) {
							@include fs(13rem);
							font-weight: 700; } }
					.method-des {
						@include fs(13rem);
						color: darken($white,60%);
						@include media-breakpoint-down(xs) {
							@include fs(12rem); } } } }
			.method-content {
				padding: 10px;
				border: 1px solid darken($white,10%);
				border-top: none;
				color: darken($white,60%);
				display: none;
				.content {
					@include fs(13rem);
					@include media-breakpoint-down(xs) {
						@include fs(12rem); } } } }
		input {
			&:checked {
				+ label {
					.method-info {
						border-color: $blue;
						.method-detail {
							border-color: $blue; } }
					.method-content {
						display: block;
						border-color: $blue; } } } } } }
.payment-agree {
	@include fs(14rem);
	margin: 10px 0; }
.booking-success {
	margin-bottom: 30px;
	text-align: center;
	.alert {
		// display: inline-block
		// width: unset
		@include fs(18rem);
		font-weight: 600;
		padding: 10px 30px;
		text-transform: uppercase;
		.fa {
			margin-right: 10px;
			@include fs(24rem);
			display: inline-block;
			vertical-align: middle; } } }
