main {
	// +pos(rel,null)
 }	// z-index: 10

.banner-caption {
	@include pos(rel,null);
	z-index: 10;
	text-align: center;
	text-shadow: 0 3px 6px color-rgba($black,0.3);
	.big {
		@include fs(36rem);
		font-weight: 700;
		line-height: 1.3;
		color: $white;
		@include media-breakpoint-down(md) {
			@include fs(32rem); }
		@include media-breakpoint-down(sm) {
			@include fs(30rem); }
		@include media-breakpoint-down(xs) {
			@include fs(28rem); } }
	.small {
		@include fs(24rem);
		color: $white;
		line-height: 1.5;
		margin-bottom: 20px;
		@include media-breakpoint-down(sm) {
			@include fs(20rem); }
		@include media-breakpoint-down(xs) {
			@include fs(16rem); } } }
.home-search {
	@include pos(rel,null);
	z-index: 10;
	.searchwrap {
		width: 65%;
		margin: 0 auto;
		@include media-breakpoint-down(sm) {
			width: 100%; }
		.searchbox {
			@include flexbox;
			padding: 0px 00px;
			@include bor-rad(50px);
			border: 2px solid $white;
			box-shadow: 0 3px 10px color-rgba($black,0.2);
			input {
				line-height: 40px;
				border: none;
				background: none;
				padding: 0px 30px;
				width: calc(100% - 60px);
				color: $white;
				&::-webkit-input-placeholder {
					color: $white;
					opacity: 0.8; }
				@include media-breakpoint-down(xs) {
 } }					// width: calc(100% - 86px)
			.searchbutton {
				@include fs(15rem);
				color: $white;
				font-weight: 500;
				line-height: 40px;
				width: 60px;
				text-align: center;
				@include media-breakpoint-down(xs) {
					@include fs(14rem);
					font-weight: 600; }
				.fa {
 } } } } }					// margin-right: 10px

.home-title {
	@include fs(30rem);
	color: $main;
	margin-bottom: 30px;
	line-height: 1.5;
	@include media-breakpoint-down(md) {
		@include fs(24rem); }
	@include media-breakpoint-down(xs) {
		@include fs(20rem); }
	strong {
		font-weight: 700;
		color: $dblue; } }

.page-title {
	@include fs(18rem);
	text-transform: uppercase;
	font-weight: 700;
	border-left: 4px solid $blue;
	line-height: 1;
	padding-left: 10px;
	margin-bottom: 20px; }

.post-title {
	@include fs(16rem);
	font-weight: 700;
	margin: 20px 0px; }

.title-wrap {
	.home-title {
		float: left; }
	.btn-viewmore {
		display: block;
		float: right;
		@include fs(16rem);
		font-weight: 300;
		color: $white;
		line-height: 40px;
		@include bor-rad(50px);
		background: $blue;
		padding: 0px 25px;
		@include media-breakpoint-down(md) {
			@include fs(14rem);
			line-height: 36px; }
		@include media-breakpoint-down(xs) {
			// float: left
			// margin-top: -20px
			// margin-bottom: 30px
			line-height: 2.3;
			padding: 0;
			color: $dblue;
			background: none; }
		&:after {
			content: '\f0da';
			margin-left: 10px; }
		&:hover {
			background: $dblue; } } }
.home-section {
	padding: 40px 0;
	background: $white;
	@include pos(rel,null);
	z-index: 1;
	@include media-breakpoint-down(md) {
		padding: 20px 0; } }
.home-intro {
	// max-width: 1350px
	margin: 0 auto;
	background: $white;
	// padding-top: 60px
	// +bor-rad(5px)
	@include media-breakpoint-down(md) {
		padding-top: 20px;
		@include bor-rad(0); }
	.intro-wrap {
		border-bottom: 1px solid darken($white,20%); }
	.home-booking-step {
		.step {
			@include flexbox;
			flex-wrap: wrap;
			margin-bottom: 60px;
			@include media-breakpoint-down(md) {
				justify-content: center; }
			@include media-breakpoint-down(xs) {
				margin-bottom: 30px;
				justify-content: flex-start; }
			.number {
				width: 60px;
				height: 60px;
				line-height: 60px;
				@include bor-rad(50px);
				color: $white;
				background: $blue;
				font-family: 'arial';
				text-align: center;
				@include fs(40rem);
				font-weight: 600;
				@include pos(abs, 0 null null 0);
				@include media-breakpoint-down(md) {
					width: 24px;
					height: 24px;
					line-height: 24px;
					@include fs(16rem);
					@include pos(static,unset);
					display: inline-block;
					vertical-align: middle;
					margin-right: 10px; } }
			.caption {
				// padding-left: 25px
				// width: calc(100% - 60px)
				padding-left: 80px;
				@include pos(rel,null);
				@include media-breakpoint-down(md) {
					padding-left: 0;
					width: 100%;
					text-align: center;
 }					// padding-top: 20px
				@include media-breakpoint-down(xs) {
					text-align: left; }
				.name {
					line-height: 60px;
					@include fs(18rem);
					font-weight: 600;
					color: $main;
					// +pos(rel,null)
					span {
						font-weight: 800; }
					@include media-breakpoint-down(md) {
						line-height: 1.5;
						margin-bottom: 10px; } }
				.des {
					@include fs(16rem);
					color: darken($white,60%);
					@include media-breakpoint-down(md) {
						@include fs(14rem); } } } } } }

.home-hotsale {
	.hotel-list {
		.hotel-card {
			padding: 1px;
			figure {
				.hotel-img {
					display: block;
					@include pos(rel,null);
					overflow: hidden;
					&:after {
						content: '';
						display: block;
						width: 0;
						height: 0;
						border-left: 25px solid transparent;
						border-right: 25px solid transparent;
						border-bottom: 25px solid darken($white,8%);
						@include pos(abs, null null 0 50%);
						@include transform(translateX(-50%)translateY(100%));
						z-index: 5;
						@include transition(all,0.5s); }
					img {
						width: 100%;
						height: 280px;
						object-fit: cover;
						-o-object-fit: cover;
						-ms-object-fit: cover;
						-moz-object-fit: cover;
						-webkit-object-fit: cover;
						@include transition(all,0.5s);
						@include media-breakpoint-down(lg) {
							height: 220px; }
						@include media-breakpoint-down(md) {
							height: 170px; }
						@include media-breakpoint-down(sm) {
							height: auto; } }
					.promotion {
						@include pos(abs, calc(100% + 50%) null null 50%);
						@include transform(translateX(-50%)translateY(-50%));
						width: 140px;
						height: 140px;
						@include bor-rad(100px);
						color: $dblue;
						background: color-rgba($white,0.9);
						@include flexbox;
						justify-content: center;
						align-items: center;
						align-content: center;
						flex-wrap: wrap;
						text-align: center;
						@include transition(all,0.5s);
						@include media-breakpoint-down(lg) {
							width: 100px;
							height: 100px; }
						@include media-breakpoint-down(md) {
							width: 80px;
							height: 80px; }
						&.sale {
							span {
								@include fs(13rem);
								display: block;
								width: 100%;
								line-height: 1; }
							strong {
								display: block;
								line-height: 1;
								width: 100%;
								@include fs(36rem);
								font-weight: 700;
								@include media-breakpoint-down(lg) {
									@include fs(28rem); }
								@include media-breakpoint-down(md) {
									@include fs(20rem); } } } } }
				figcaption {
					background: darken($white,8%);
					padding: 30px;
					@include media-breakpoint-down(lg) {
						padding: 15px; }
					.hotel-name {
						@include fs(19rem);
						line-height: 1.3;
						font-weight: 600;
						margin-bottom: 15px;
						height: calc((1.1875rem*1.3*2) + (0.8125rem*1.3*1) + 5px);
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
						overflow: hidden;
						text-overflow: ellipsis;
						@include media-breakpoint-down(lg) {
							@include fs(16rem);
							height: calc((1rem*1.3*2) + (0.8125rem*1.3*1) + 5px); }
						a {
							color: $main; } }
					.hotel-des {
						@include fs(13rem);
						font-weight: 500;
						line-height: 1.3;
						margin-top: 5px; }
					.hotel-price {
						&:before {
							content: '';
							display: block;
							width: 30px;
							height: 4px;
							background: $dblue;
							margin-bottom: 15px; }
						.old {
							@include fs(13rem);
							font-weight: 500;
							@include media-breakpoint-down(lg) {
								@include fs(12rem); } }
						.new {
							@include fs(25rem);
							font-weight: 700;
							@include media-breakpoint-down(lg) {
								@include fs(18rem); }
							span {
								@include fs(13rem);
								font-weight: 600;
								@include media-breakpoint-down(lg) {
									@include fs(12rem); } } } } }
				&:hover {
					.hotel-img {
						&:after {
							@include transform(translateX(-50%)translateY(0)); }
						img {
							filter: brightness(70%); }
						.promotion {
							top: 50%; } }
					figcaption {
						.hotel-name {
							a {
								color: $dblue; } } } } } } } }

.home-place {
	.place-wrap {
		@include pos(rel,null);
		// background: lighten($blue,30%)
		overflow: hidden;
		.background {
			@include pos(abs, 0 null 0 0);
			right: 0;
			opacity: 0;
			filter: blur(0px); } }
	.home-place-slide {
		@include pos(rel,null);
		z-index: 10;
		overflow: hidden;
		.row {
			margin: 0px -1px; }
		.place-card {
			padding: 0 1px;
			background: $white;
			figure {
				.place-img {
					display: block;
					img {
						width: 100%; } }
				figcaption {
					padding: 20px 30px;
					background: darken($white,8%);
					@include media-breakpoint-down(lg) {
						padding: 10px 15px; }
					.place-name {
						@include fs(18rem);
						font-weight: 600;
						@include media-breakpoint-down(lg) {
							@include fs(14rem); }
						a {
							color: $main; } } } } } } }
.home-news {
	.news-list {
		.news-card {
			@include media-breakpoint-down(xs) {
				margin-bottom: 15px; }
			figure {
				.news-img {
					display: block;
					img {
						width: 100%; } }
				figcaption {
					padding: 20px 0;
					.news-cate {
						margin-bottom: 10px;
						a {
							display: block;
							color: $main;
							@include fs(13rem); } }
					.news-name {
						margin-bottom: 15px;
						@include media-breakpoint-down(xs) {
							margin-bottom: 5px; }
						a {
							display: block;
							@include fs(19rem);
							line-height: 1.3;
							font-weight: 600;
							margin-bottom: 15px;
							height: 1.1875rem*1.3*3;
							-webkit-line-clamp: 3;
							-webkit-box-orient: vertical;
							overflow: hidden;
							text-overflow: ellipsis;
							color: $main;
							@include media-breakpoint-down(lg) {
								@include fs(16rem);
								height: 1rem*1.3*3; }
							@include media-breakpoint-down(xs) {
								@include fs(14rem);
								height: unset;
								margin-bottom: 5px; } } }
					time {
						color: darken($white,40%);
						@include fs(13rem); } }
				&:hover {
					figcaption {
						.news-name {
							a {
								color: $dblue; } } } } } } } }
