.visa-step {
	background: darken($white,0%);
	margin: 39px 0;
	overflow: hidden;
	@include bor-rad(10px);
	@include fs(13rem);
	border: 1px solid darken($white,10%);
	@include flexbox;
	.step {
		width: 25%;
		line-height: 40px;
		padding: 0px 0px 0px 20px;
		@include pos(rel,null);
		@include flexbox;
		align-content: center;
		align-items: center;
		&:before {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-top: 22px solid transparent;
			border-bottom: 22px solid transparent;
			border-right: 0 solid transparent;
			border-left: 15px solid darken($white,10%);
			@include pos(abs, -2px 0px null null);
			@include transform(translateX(15px));
			z-index: 10; }
		&:after {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-top: 22px solid transparent;
			border-bottom: 22px solid transparent;
			border-right: 0 solid transparent;
			border-left: 15px solid $white;
			@include pos(abs, -2px 1px null null);
			@include transform(translateX(15px));
			z-index: 11; }
		&:last-child {
			&:before, &:after {
				display: none; } }
		.number {
			display: inline-block;
			width: 26px;
			height: 26px;
			line-height: 26px;
			text-align: center;
			background: darken($white,5%);
			color: $main;
			@include bor-rad(50px); }
		.name {
			padding-left: 10px;
			color: darken($white,60%);
			@include fs(13rem); }
		&.active {
			background: $blue;
			&:before, &:after {
				border-left-color: $blue; }
			.number {
				background: $white;
				color: $blue;
				font-weight: 600; }
			.name {
				color: $white;
				font-weight: 500; } }
		&.finish {
			background: darken($white,5%);
			&:after {
				border-left-color: darken($white,5%); }
			.number {
				@include fs(0);
				&:before {
					content: '\f00c';
					@include fs(13rem);
					color: $green; } } } } }

.visa-page {
	margin: 40px 0;
	.cart-success {
		@include fs(18rem); }
	.page-group {
		margin-bottom: 20px; }
	.small-title {
		@include fs(16rem);
		font-weight: 500;
		margin-bottom: 15px; }
	.total-amount {
		text-align: right;
		margin-bottom: 30px;
		strong {
			@include fs(20rem);
			font-weight: 600;
			color: $red;
			margin-left: 10px; } }
	.visa-process {
		@include bor-rad(5px);
		background: darken($white,3%);
		margin-bottom: 15px;
		padding: 15px;
		ul {
			li {
				display: inline-block;
				&:after {
					content: '\f105';
					color: $blue;
					font-weight: 600;
					margin: 0px 10px; }
				&:last-child {
					&:after {
						display: none; } } } } }
	.expected-date {
		.embassy {
			margin-bottom: 15px;
			.btn-embassy {
				@include bor-rad(5px);
				padding: 8px 15px;
				background: $blue;
				color: $white;
				@include fs(13rem);
				&:before {
					content: '\f133';
					margin-right: 5px; }
				&:after {
					display: none; } } }
		.embassy-table {
			border: 1px solid darken($white,10%);
			width: 100%;
			margin-bottom: 20px;
			th, td {
				padding: 10px;
				@include fs(13rem);
				border-bottom: 1px solid darken($white,10%);
				vertical-align: middle;
				width: 33.33%;
				text-align: center;
				+ th, + td {
					border-left: 1px solid darken($white,10%); } }
			th {
				font-weight: 500;
				background: darken($white,3%); } } }
	.table-wrap {
		@include media-breakpoint-down(xs) {
			width: 100%;
			overflow: auto;
			table {
				min-width: 600px; } } }
