 }			// 		font-weight: 500
		ul.visa-tabs {
			border-bottom: 2px solid $blue;
			text-align: right;
			li {
				a {
					display: block;
					padding: 8px 15px;
					@include bor-rad(0);
					background: darken($white,5%);
					color: $main;
					@include fs(14rem); }
				&:hover {
					a {
						color: $blue;
						background: lighten($blue,40%); } }
				&.r-tabs-state-active {
					a {
						background: $blue;
						color: $white; } } } }
		.visa-table {
			border: 1px solid darken($white,10%);
			width: 100%;
			margin-bottom: 20px;
			th, td {
				padding: 10px;
				@include fs(13rem);
				border-bottom: 1px solid darken($white,10%);
				vertical-align: middle;
				+ th, + td {
					border-left: 1px solid darken($white,10%); } }
			th {
				font-weight: 500;
				background: darken($white,3%); }
			.total {
				font-weight: 600;
				color: $red; }
