				display: block;
				background: $blue;
				color: $white;
				@include bor-rad(5px);
				padding: 5px;
				text-align: center;
				&:before {
					content: '\f044';
					margin-right: 5px; }
				&:hover {
					background: darken($blue,10%); } } } }
	.visa-notice {
		@include fs(14rem);
		@include bor-rad(5px);
		padding: 15px;
		// border: 1px solid darken($white,10%)
		background: darken($white,3%); }
	.order-form {
		.visa-img {
			border: 1px solid darken($white,10%);
			padding: 10px; }
		.form-wrap {
			table {
				width: 100%;
				border: 1px solid darken($white,10%);
				margin-bottom: 20px;
				th, td {
					vertical-align: middle;
					padding: 10px;
					@include fs(14rem);
					border-bottom: 1px solid darken($white,10%);
					+ th, + td {
						border-left: 1px solid darken($white,10%); }
					&:first-child {
						background: darken($white,3%);
						font-weight: 500; }
					.total {
						font-weight: 500;
						color: $red; } }
				.date {
					&:after {
						content: '\f133';
						@include pos(abs, 10px 10px null null); } }
				.form-group {
					display: inline-block;
					@include pos(rel,null);
					select, input {
						width: 200px; } } }
			.form-group {
				margin-bottom: 10px;
				@include flexbox;
				label {
					width: 150px;
					@include fs(14rem);
					@include flexbox;
					align-items: center;
					align-content: center; }
				input, textarea, select {
					width: calc(100% - 150px);
					padding: 10px 15px;
					border: 1px solid darken($white,10%);
					@include bor-rad(3px);
					@include fs(14rem); }

				ul {
					width: calc(100% - 150px + 20px);
					padding: 8px 0;
					@include flexbox;
					align-items: center;
					margin: 0px -10px;
					li {
						flex: 1;
						padding: 0px 10px;
						select {
							width: 100%; } } } }
			.frm-btn {
				text-align: center; }
			.btn-book {
				@include bor-rad(5px);
				background: $blue;
				color: $white;
				padding: 8px 20px;
				display: inline-block;
				&:hover {
					background: darken($blue,10%); }
				&:before {
					content: '\f044';
					margin-right: 5px; } } } }
	.extra-service {
		@include fs(14rem);
		color: darken($white,60%);
		margin-bottom: 10px;
		.form-group {
			margin-bottom: 10px;
 }			// display: none
		textarea {
			border: 1px solid darken($white,10%);
			@include bor-rad(5px);
			padding: 10px 15px;
			width: 100%; }
		a {
			color: $blue;
			@include fs(16rem);
			font-weight: 500;
			// text-transform: uppercase
			&:after {
				content: '\f105';
				margin-left: 5px; }
			&:hover {
				color: lighten($red,10%); } }
		.total-amount {
			text-align: right;
			strong {
				color: $red;
				@include fs(24rem);
				font-weight: 600; } } }
	.cartbtn-wrap {
		@include flexbox;
		justify-content: center;
		.btn-backstep {
			display: block;
			background: darken($white,10%);
			color: $main;
			@include bor-rad(3px);
			padding: 8px 20px;
			margin-right: 20px;
			&:hover {
				background: darken($white,20%); } }
		.btn-nextstep {
			display: block;
			background: $blue;
			color: $white;
			@include bor-rad(3px);
			padding: 8px 20px;
			&:hover {
