
.bookingpage {
	padding-top: 0;
	header, footer {
		display: none; } }
.hide-button {
	.mini-cart {
		.cart-table {
			.remove {
				display: none; } }
		.btn-wrap {
			display: none; } } }
.cart-table {
	width: 100%;
	@include bor-rad(5px);
	border: 1px solid darken($white,10%);
	margin-bottom: 30px;
	th, td {
		@include fs(13rem);
		padding: 10px;
		vertical-align: middle;
		&:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6) {
			white-space: nowrap;
			@include media-breakpoint-down(xs) {
				white-space: normal; } } }
	th {
		font-weight: 500;
		background: lighten($blue,40%);
		border-bottom: 2px solid $blue; }
	td {
		border-bottom: 1px solid darken($white,10%);
		&.type {
			background: darken($white,5%);
			font-weight: 500; }
		&.money {
			color: $blue;
			font-weight: 500; }
		.name {
			font-weight: 500; }
		time {
			color: darken($white,40%); }
		.remove {
			@include fs(16rem);
			color: lighten($red,10%); } }
	tr {
		&:last-child {
			td {
				border-bottom: none; } } } }
.pc-cart-table {
	@include media-breakpoint-down(md) {
		display: none; } }
.mobile-cart-table {
	display: none;
	border: 1px solid darken($white,10%);
	@include bor-rad(5px);
	overflow: hidden;
	margin-bottom: 20px;
	@include media-breakpoint-down(md) {
		display: block; }
	tbody, tr {
		width: 100%;
		display: block; }
	th, td {
		display: block;
		width: 100%;
		@include fs(14rem);
		border-bottom: 1px solid darken($white,10%);
		@include pos(rel,null);
		@include media-breakpoint-down(xs) {
			@include fs(13rem); } }
	th {
		background: darken($white,3%);
		@include fs(15rem);
		padding: 8px 15px;
		@include media-breakpoint-down(xs) {
			@include fs(14rem);
			padding: 8px 10px; } }
	td {
		padding: 15px;
		@include media-breakpoint-down(xs) {
			padding: 10px; } }
	.product-name {
		@include bor-rad(5px);
		background: lighten($blue,40%);
		border: 1px solid lighten($blue,30%);
		padding: 10px;
		padding-right: 20px;
		.name {
			@include fs(15rem);
			font-weight: 700;
			color: $main;
			@include media-breakpoint-down(xs) {
				@include fs(13rem); } }
		time {
			@include fs(13rem);
			color: darken($white,60%);
			@include media-breakpoint-down(xs) {
				@include fs(12rem); } } }
	.product-detail {
		padding: 15px 0 0;
		ul {
			padding-left: 20px;
			list-style-type: disc;
			li {
				@include fs(13rem);
				@include media-breakpoint-down(xs) {
					@include fs(12rem); } } } }
	.remove {
		color: $red;
		@include pos(abs, 20px 25px null null);
		z-index: 5;
		@include fs(18rem); } }
.cart-page {
	.mini-cart {
		@include pos(rel,null); }
	.btn-edit {
		@include pos(abs, 0 0 null null);
		@include fs(14rem);
		color: $blue;
		&:before {
			content: '\f040';
			margin-right: 5px; } }
	.cart-form {
		margin-bottom: 20px;
		.form-group {
			@include flexbox;
			@include media-breakpoint-down(xs) {
				flex-wrap: wrap;
				margin-bottom: 5px; }
			label {
				width: 150px;
				@include fs(14rem);
				@include flexbox;
				align-items: center;
				align-content: center;
				@include media-breakpoint-down(xs) {
					width: 100%;
					@include fs(13rem); } }
			.input-name {
				width: calc(100% - 150px);
				@include media-breakpoint-down(xs) {
					width: 100%; } }
			input, textarea, select {
				width: calc(100% - 150px);
				padding: 10px 15px;
				border: 1px solid darken($white,10%);
				@include bor-rad(3px);
				@include fs(14rem);
				@include media-breakpoint-down(xs) {
					width: 100%;
					@include fs(13rem); } }

			ul {
				width: calc(100% - 150px + 20px);
				padding: 8px 0;
				@include flexbox;
				align-items: center;
				margin: 0px -10px;
				@include media-breakpoint-down(xs) {
					width: calc(100% + 20px); }
				li {
					flex: 1;
					padding: 0px 10px;
					select {
						width: 100%; } } }
			.value {
				width: calc(100% - 150px);
				font-weight: 500;
				@include fs(14rem);
				@include media-breakpoint-down(xs) {
					width: 100%; }
				span {
					font-weight: 500; } }
			.radio {
				label {
					width: 80px; } } } }
	.total-amount {
		text-align: right;
		margin-bottom: 30px;
		strong {
			@include fs(20rem);
			font-weight: 600;
			color: $red;
			margin-left: 10px; } }
	.btn-wrap {
		margin-bottom: 20px;
		@include flexbox;
		justify-content: center;
		.btn-back {
			// float: left
			@include fs(14rem);
			font-weight: 500;
			background: darken($white,5%);
			color: $main;
			line-height: 40px;
			padding: 0px 20px;
			@include bor-rad(5px);
			&:hover {
				background: darken($white,15%); } }
		.btn-continue {
			// float: right
			margin-left: 20px;
			@include fs(14rem);
			font-weight: 500;
			background: $blue;
			color: $white;
			line-height: 40px;
			padding: 0px 40px;
			@include bor-rad(5px);
			&:hover {
				background: darken($blue,10%); } } }
	.method-list {
		margin-bottom: 15px; }
	.payment-agree {
		margin-bottom: 30px; }
	.cart-success {
		text-align: center;
		margin-bottom: 30px;
		font-weight: 500; } }

