// REGULAR COMMON

.RadCaptcha {
	margin-bottom: 15px;
	#ctl00_altContent2_ctl01_captcha_ctl01, #ctl00_mainContent_RegisterUser_CreateUserStepContainer_captcha_ctl01, #ctl00_mainContent_login1_LoginCtrl_captcha_ctl01 {
		float: left;
		+ p {
			float: left;
			margin-top: 0; } }
	span {
		@include fs(13rem);
		color: $red;
		margin-bottom: 5px; }
	p {
		margin-top: 5px; }
	img {
		display: inline-block!important;
		margin-right: 0.9375rem;
		vertical-align: middle; }
	input {
		width: 250px; }
	label {
		display: none; }
	.rcRefreshImage {
		display: inline-block!important;
		vertical-align: middle;
		position: relative;
		color: transparent;
		width: 30px;
		height: 30px;
		@include fs(0);
		&:after {
			content: '\f021';
			font-family: "FontAwesome";
			font-size: 18px;
			color: $black;
			position: absolute;
			top: 50%;
			right: 0;
			left: 0;
			@include fs(18rem);
			@include transform(translate(0, -50%)); } }
	#ctl00_mainContent_ctl03_captcha_ctl01 {
		float: left;
		+ p {
			display: table-cell;
			padding: 7px 0;
			input {
				width: 100%; } } } }

.ActionMenu {
	z-index: 99999 !important;
	.ActionMenuTag {
		line-height: 1.5;
		.fa {
			color: #1B4D79!important;
			width: auto!important;
			height: auto!important;
			border: none!important;
			font-size: 11px!important;
			display: inline-block!important;
			float: left!important;
			border-radius: 0!important;
			line-height: 1.1rem!important;
			margin-right: 4px!important; } }
	.ActionMenuBody {
		z-index: 99999 !important;
		> dl dd > a {
			color: #1B4D79!important;
			width: auto!important;
			height: auto!important;
			background: transparent;
			.fa {
				color: #1B4D79!important;
				width: auto!important;
				height: auto!important;
				border: none!important;
				font-size: 11px!important;
				display: inline-block!important;
				float: left!important;
				border-radius: 0!important;
				line-height: 1.1rem!important;
				margin-right: 4px!important; } }
		> h4 {
			color: #333!important; } } }
.cmsadminpanel {
	z-index: 16000009; }
.alert {
	display: block;
	margin: 10px 0; }
/*Register Form*/
.subscribe {
	.alert-success {
		font-size: 0.875rem;
		line-height: 1.275rem;
		font-style: italic;
		display: block;
		width: 100%;
		text-align: center;
		color: $red;
		@include fs(18rem);
		border: none;
		background-color: rgba(255, 255, 255, 0.8);
		padding: 10px;
		margin: 0;
		border-radius: 0; } }
.subscribefrm {
	position: relative;
	.fa.fa-exclamation-triangle {
		color: red;
		@include fs(15rem);
		display: block;
		position: absolute;
		top: 13px;
		right: 90px;
		text-indent: -9999px;
		width: 15px;
		height: 15px;
		line-height: 15px;
		text-align: center;
		&:after {
			content: '\f071';
			font-size: 12px;
			color: red;
			text-indent: 0;
			float: left;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0; } }
	.fa-exclamation-triangle:before, .fa-warning:before {
		margin-right: 5px; } }
.form-group {
	position: relative;
	.fa.fa-exclamation-triangle {
		position: absolute;
		bottom: 10px;
		right: 15px;
		color: transparent;
		width: 15px;
		height: 15px;
		&:before {
			content: '\f071';
			font-size: 12px;
			font-weight: 500;
			color: red;
			position: absolute; } }
	.fa-exclamation-triangle:before, .fa-warning:before {
		margin-right: 5px; } }
.contact-form {
	.form-group {
		.fa.fa-exclamation-triangle {
			top: 36px;
			right: 20px;
			@include fs(18rem); } } }

.hidden {
	display: none; }

.share {
	text-align: left;
	clear: both;
	&.sc-right {
		text-align: right; }
	.face-like {
		width: auto;
		display: inline-block; }
	> div, .face-like, .fb-share-button, .google {
		display: inline-block;
		margin-bottom: 5px;
		margin-right: 5px;
		vertical-align: top; }
	.google, .tweet {
		width: 60px; }
	.fb-share-button.fb_iframe_widget iframe, iframe.twitter-share-button {
		display: block; }
	.google {
		margin-top: -5px; } }

// **********SEARCH-PAGE**********
.searchresults {
	.pagination {
		display: none; }
	.altcontent1 {
		display: none; }
	.pagination {
		text-align: left; }
	.banner-home {
		display: none; }
	.product-page {
		width: 100%;
		.product-list {
			.col-xs-12 {
				padding: 0;
				width: 20%;
				@include media-breakpoint-down(lg) {
					width: 20%; }
				@include media-breakpoint-down(md) {
					width: 25%; }
				@include media-breakpoint-down(sm) {
					width: 33.33%; }
				@include media-breakpoint-down(xs) {
					width: 50%; } } } } }
.search-page {
	margin: 40px 0;
	.searchresultsummary {
		display: none; }
	.page-header {
		border: none;
		padding: 0;
		margin: 0; }
	h1 {
		font-size: 21px;
		font-weight: 700;
		margin: 0 0 15px;
		text-transform: none;
		line-height: 1.5em;
		color: $black;
		display: inline-block; }
	.btnSearchWrap {
		position: relative;
		.btnSearch {
			@include opacity(1);
			position: absolute;
			top: 0;
			left: 0;
			text-align: center;
			bottom: 0;
			width: 34px;
			.fa {
				position: absolute;
				right: 0;
				display: block;
				top: 0;
				bottom: 0;
				left: 0;
				margin: 0 auto;
				width: 34px;
				height: 34px;
				line-height: 34px;
				border: 1px solid darken($white, 15%);
				vertical-align: middle; } }
		.seachpage-btn {
			position: relative;
			height: 34px;
			width: 34px;
			@include opacity(0); } }
	.btn {
		background-color: $blue;
		color: $white;
		border: none;
		@include transition(all,.3s);
		&:hover,  &:focus {
			background-color: darken($blue,5%); } }
	.searchsummary {
		margin: 10px 0 25px;
		display: none;
		strong {
			color: $black; } }
	.searchcontrols {
		margin-bottom: 30px;
		input[type="text"] {
			height: 35px;
			line-height: 35px;
			padding: 0 15px;
			border: 1px solid darken($white, 15%);
			font-size: 0.9375rem;
			color: $black;
			@include border-radius(0); }
		input[type="submit"] {
			border: none;
			height: 35px;
			@include bor-rad(0);
			line-height: 35px;
			text-align: center;
			padding: 0 20px;
			color: $white;
			background-color: $pink;
			&:hover {
				background: darken($pink,5%); } } }
	.searchresultlist {
		margin-top: 20px; }
	.searchresult {
		border-bottom: 1px solid darken($white, 15%);
		margin: 0 0 15px;
		padding: 0 0 15px;
		h3 {
			font-size: 16px;
			font-weight: 600;
			margin: 0 0 10px;
			color: $black;
			a {
				color: $blue;
				text-decoration: none;
				&:hover {
					color: $red; } } } }
	hr {
		display: none; } }


// SEARCH SUGGEST
.suggestsearch {
	position: absolute!important;
	top: calc(100% + 3px);
	left: 0;
	width: 100%;
	z-index: 401;
	background: $white;
	box-shadow: 0 3px 30px color-rgba($black,0.2);
	ul {
		margin: 0;
		padding: 0;
		li {
			list-style: none;
			display: block;
			border-bottom: 1px dotted darken($white, 10%);
			&:before, &:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			&:hover,
			&.selected {
				background: darken($white, 10%); }

			&.prdlist_icon {
				margin-bottom: 0;
				height: auto; }
			.product-img {
				width: 70px;
				height: 70px;
				overflow: hidden;
				display: flex;
				justify-content: center;
				align-content: center;
				float: left;
				background: $white;
				border: 1px solid darken($white,10%);
				img {
					max-height: 100%;
					max-width: 100%; } }
			.prd-info {
				width: calc(100% - 70px);
				padding: 10px;
				float: left; }
			a {
				padding: 10px;
				display: block;
				.prd-info {
					text-align: left;
					padding: 0 10px;
					h3,
					h4 {
						@include fs(13rem);
						color: darken($white,40%);
						margin: 0;
						line-height: 1.5; }

					h3 {
						@include fs(15rem);
						color: $pink;
						font-weight: 500; }
					span {
						display: block;
						&.price {
							color: $black; }
						&.price_original {
							color: $black;
							text-decoration: line-through; } } } } } } }

.checkbox {
	input {
		display: none;
		+ label {
			color: $main;
			padding-left: 25px;
			@include pos(rel,null);
			&:hover {
				color: $black; }
			&:before {
				content: '';
				display: block;
				width: 16px;
				height: 16px;
				@include bor-rad(3px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 2px null null 0); }
			&:after {
				content: '\f00c';
				font-family: 'fontawesome';
				display: block;
				width: 16px;
				height: 16px;
				line-height: 14px;
				text-align: center;
				@include bor-rad(3px);
				color: $red;
				border: 1px solid darken($white,20%);
				@include pos(abs, 2px null null 0);
				z-index: 1;
				@include transition(all,0.5s);
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
.radio {
	input {
		display: none;
		+ label {
			color: $main;
			padding-left: 25px;
			@include pos(rel,null);
			width: auto;
			&:hover {
				color: $black; }
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(30px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 3px null null 0); }
			&:after {
				content: '';
				font-family: 'fontawesome';
				display: block;
				width: 14px;
				height: 14px;
				line-height: 14px;
				text-align: center;
				@include bor-rad(30px);
				color: $red;
				border: 4px solid lighten($red,10%);
				@include pos(abs, 3px null null 0);
				z-index: 1;
				@include transition(all,0.5s);
				opacity: 0; } }
		&:checked {
			+ label {
				color: $main;
				&:after {
					opacity: 1; } } } } }

#map {
	iframe {
		pointer-events: none;
		&.active {
			pointer-events: all; } } }

.modal-open {
	padding-right: 0 !important; }

.r-tabs {
	.r-tabs-panel {
		padding: 0; } }
