// MATERIAL VARIABLE
@mixin flexbox {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flexbox;
	display: flex; }
@mixin clearfix {
	content: '';
	display: table;
	width: 100%;
	clear: both; }
@mixin fs($size) {
	font-size: ($size / 16); }
@mixin tof($fs,$lh,$ls) {
	height: ($fs / 16)*$lh*$ls;
	font-size: ($fs / 16);
	line-height: $lh;
	-webkit-line-clamp: $ls;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis; }
body {
	margin: 0;
	padding: 0;
	@include fs(15rem);
	// font-family: 'Muli'
	font-family: 'Montserrat';
	color: $main;
	line-height: 1.5; }
html {
	overflow: auto; }
.fullpage {
	width: 100%;
	overflow: hidden;
	// overflow: hidden
	// max-width: 1600px
 }	// margin: 0 auto
a {
	@include transition(all,0.3s); }
a, a:hover {
	&:focus, &:active, &:hover {
		outline: none;
		text-decoration: none; } }
input,button, select, textarea {
	@include transition(all,0.3s);
	&:hover, &:active, &:focus {
		outline: none; } }
button {
	border: none;
	background: none;
	margin: 0;
	padding: 0;
	@include transition(all,0.3s); }
img, iframe {
	vertical-align: middle; }
main {
	clear: both;
	&:before, &:after {
		content: '';
		display: table;
		width: 100%;
		clear: both; } }
strong {
	span {
		font-weight: bold; } }
.container {
	@include pos(static,null);
	@include media-breakpoint-up(xl) {
		max-width: 1170px;
		width: 100%; }
	@include media-breakpoint-down(md) {
		max-width: 100%;
		width: 100%; } }

.content {
	line-height: 1.5;
	+ .share {
		margin: 15px 0; }
	@include media-breakpoint-down(md) {
		@include fs(14rem);
		line-height: 1.8; }
	ul, ol, p, h1, h2, h3, h4, h5, h6, img, figure {
		margin-bottom: 15px;
		line-height: 1.5;
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; } }
	figure {
		margin: 0 auto 15px;
		text-align: center;
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; }
		img {
			margin-bottom: 5px; }
		figcaption {
			// background: darken($white,5%)
			padding: 5px 0;
			text-align: center;
			@include fs(13rem);
			font-style: italic;
			color: darken($white,60%); } }
	h5 {
		@include fs(18rem);
		font-weight: 300;
 }		// margin: 15px 0
	h6 {
		@include fs(16rem); }

	.blue {
		color: $blue; }
	ul {
		padding-left: 15px;
		list-style-type: disc;
