		background: darken($white,10%);
		margin-bottom: 20px; } }


.test {
	figure {
		@include pos(rel,null);
		overflow: hidden;
		.test-img {
			display: block; }
		.name {
			@include pos(abs, null 15px 15px 15px);
			font-weight: 500;
			@include fs(18rem);
			color: $white;
			@include transition(all,0.2s); }
		figcaption {
			.test-overlay {
				@include pos(abs, 0 null 0 0);
				right: 0;
				background: color-rgba($blue,0.8);
				@include transform(translateY(100%));
				@include transition(all,0.5s); }
			.test-info {
				@include pos(abs, null 0 0 0);
				padding: 15px;
				@include transform(translateY(100%));
				@include transition(all,0.5s);
				.test-name {
					font-weight: 500;
					@include fs(18rem);
					margin-bottom: 10px;
					a {
						color: $white; } }
				time {
					color: $white; }
				.test-price {
					color: $white; } } }
		&:hover {
			.name {
				opacity: 0; }
			.test-overlay, .test-info {
				@include transform(translateY(0%)); } } } }
.modal-booktour {
	.modal-dialog {
		max-width: 500px; } }

.tour-price-table {
	@include bor-rad(5px);
	overflow: hidden;
	border: 1px solid darken($white,10%);
	display: block;
	margin-bottom: 30px;
	tbody, thead, tr {
		display: block;
		width: 100%; }
	th {
		display: block;
		@include fs(14rem);
		background: darken($white,3%);
		padding: 8px 15px;
		width: 100%; }
	td {
		padding: 15px;
		.tour-name {
			@include fs(15rem);
			font-weight: 600; }
		time {
			@include fs(12rem);
			display: block;
			margin-bottom: 10px;
			&:before {
				content: '\f017';
				margin-right: 5px; } }
		.tour-des {
			ul {
				li {
					@include fs(14rem);
					span {
						@include fs(16rem);
						font-weight: 600;
						color: lighten($red,10%);
						margin-left: 5px; } } } } } }
